* {
  box-sizing: border-box;
}

html {
  --header-height: 60px;
  --link-hover-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-weight: 500;
}

@media (width >= 1024px) {
  .header {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    margin: 0 auto;
    padding: 0 50px;
    display: flex;
  }

  .nav {
    height: 100%;
  }

  .logo {
    height: 40px;
    display: block;
  }

  .nav__list {
    flex-direction: row;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
  }

  .nav__link {
    align-items: center;
    display: flex;
  }

  .link {
    text-transform: uppercase;
    color: #000;
    font-size: 12px;
    line-height: var(--header-height);
    margin: 0 10px;
    text-decoration: none;
    position: relative;
  }

  .link:hover {
    color: var(--link-hover-color);
  }

  .nav__link:first-child .link {
    margin-left: 0;
  }

  .nav__link:last-child .link {
    margin-right: 0;
  }

  .link.is-active {
    color: var(--link-hover-color);
  }

  .link.is-active:after {
    content: "";
    background-color: var(--link-hover-color);
    border-radius: 8px;
    width: 100%;
    height: 4px;
    position: absolute;
    bottom: 0;
    left: 0;
  }
}

